/* ==========================================================================
   Default WordPress widgets styles - begin
   ========================================================================== */

.widget {
    margin: 0 0 $widget-bottom-margin;
    padding: 0;

    .edge-widget-title-holder {
        margin-bottom: $widget-title-margin-bottom;

        .edge-widget-title {
            color: $widget-heading-color;
            margin-top: 0;
            margin-bottom: 0;
        }

        .edge-separator {
            border-color: $widget-text-color;
            border-bottom-width: 3px;
            width: 30px;
        }
    }

    ul {
        padding: 0;
        margin: 0;
        list-style: none;

        li {
            display: block;
            line-height: $widget-list-item-height;
            margin: 0;
            padding: $widget-list-item-padding 0;

            &:last-child {
                border: 0;
                padding-bottom: 0;
            }

            a {
                color: $widget-link-color;

                &:hover {
                    color: $widget-link-hover-color;
                }
            }
        }

        .children,
        .sub-menu {
            margin-top: $widget-list-item-padding;
            padding-left: 15px;
        }
    }

    // widgets with lists and animation on link hover
    &.widget_pages,
    &.widget_archive,
    &.widget_categories,
    &.widget_meta,
    &.widget_recent_entries,
    &.widget_nav_menu {

	    .edge-widget-title-holder {
            margin-bottom: 11px;
	    }

        ul {

            li {
                overflow: hidden;
                padding: 7px 0;

                a {
                    display: inline;
                    overflow: hidden;
                    padding: 0;
                    position: relative;
                    color: $widget-link-color;
                    font-family: $default-heading-font;
                    font-weight: 700;
                    text-transform: uppercase;

                    @include edgeTransition(padding .2s ease);

                    &:hover {
                        color: $widget-link-hover-color;
                    }
                }
            }
        }
    }

    &.widget_nav_menu {
        text-transform: uppercase;
    }

    // widgets with lists and w/o animation on link hover
    &.widget_recent_comments,
    &.widget_rss {

        .edge-widget-title {
            margin-bottom: 0;
        }
    }

    // calendar
    #wp-calendar {
        text-align: center;
        width: 100%;

        caption {
            margin: 0 0 $widget-list-item-padding;

        }

        th, td {
            padding: 3px 4px;
        }

        td#today {
            color: $widget-text-color;
            background-color: $widget-list-item-background-color;
        }

        tfoot a {
            color: $widget-link-color;

            &:hover {
                color: $widget-link-hover-color;
            }
        }
    }

    // rss
    &.widget_rss {

        .rss-widget-icon {
            display: inline-block;
        }

        .rss-date {
            display: block;
            margin-bottom: $widget-list-item-padding;
        }

        cite {
            display: block;
            margin-top: calc(#{$widget-list-item-padding} / 2);
        }
    }

    // search
    &.widget_search {

        .input-holder {
            border-bottom: 2px solid $widget-search-border-color;

            input,
            button {
                background-color: transparent;
                border: 0;
                border-radius: 0;
                box-sizing: border-box;
                color: #000;
                display: inline-block;
                float: left;
                font-family: $default-text-font;
                font-size: 14px;
                font-weight: inherit;
                height: $widget-search-form-height;
                line-height: calc(#{$widget-search-form-height} - 2px);
                outline: 0;
                position: relative;
                vertical-align: top;
                -webkit-appearance: none;

                @include edgeTransition(color .2s ease-in-out);
            }

            input.search-field {
                width: calc(100% - 19px);

                @include edgePlaceholder {
                    color: $widget-text-color;
                }
            }

            input.search-field::-webkit-search-cancel-button {
                display: none;
            }

            button {
                background: $widget-list-item-background-color;
                color: $widget-search-border-color;
                cursor: pointer;
                font-size: 16px;
                width: 19px;
                padding: 0;

                @include edgeTransition(color .2s ease-in-out);

                &:hover {
                    color: $widget-link-hover-color;
                }

                span {
                    display: block;
                    left: 0;
                    line-height: inherit;
                    position: absolute;
                    top: 0;
                    width: 100%;

                    @include edgeTransform(scaleX(-1));

                    &:before {
                        display: block;
                        line-height: inherit;
                    }
                }
            }
        }
    }

    // search post type
    &.edge-search-post-type-widget {
        .edge-post-type-search-field {
            border-radius: 3px;
            border-color: $widget-list-item-border-color;
            border-style: $widget-list-item-border-style;
            border-width: $widget-list-item-border-width;
            background-color: transparent;
            box-sizing: border-box;
            color: $widget-text-color;
            display: inline-block;
            float: left;
            font-family: $default-text-font;
            font-size: 15px;
            font-weight: inherit;
            height: $widget-search-form-height;
            line-height: calc(#{$widget-search-form-height} - 2px);
            outline: 0;
            position: relative;
            vertical-align: top;
            -webkit-appearance: none;
            width: 100%; /* width of loading icon */

            @include edgeTransition(color .2s ease-in-out);

            @include edgePlaceholder {
                color: $widget-text-color;
            }
        }
    }

    // combobox
    &.widget_archive,
    &.widget_categories,
    &.widget_text {

        select {
            background-color: transparent;
            border-color: $widget-list-item-border-color;
            border-style: $widget-list-item-border-style;
            border-width: $widget-list-item-border-width;
            border-radius: 0;
            box-sizing: border-box;
            color: $widget-text-color;
            display: inline-block;
            font-family: $default-text-font;
            font-size: 15px;
            font-weight: inherit;
            height: $widget-search-form-height;
            line-height: calc(#{$widget-search-form-height} - 2px);
            margin-top: $widget-list-item-padding;
            outline: 0;
            padding: 0 0 0 21px;
            position: relative;
            vertical-align: top;
            width: 100%;

            @include edgeTransition(color .2s ease-in-out);

            option {
            }
        }
    }

    &.widget_categories {
        a {
            &:after {
                position: absolute;
                content: '';
                left: 0;
                bottom: 0;
                width: 100%;
                height: 1px;
                background-color: $default-heading-color;
            }
        }
    }

    // tag cloud
    &.widget_tag_cloud {

        .edge-widget-title-holder {
            margin-bottom: 13px;
        }

        a {
            position: relative;
            border-radius: 2px;
            color: $widget-link-color;
            display: inline-block;
            margin-right: 7px;
            font-size: 14px !important;
            font-family: $default-heading-font;
            font-weight: 700;
            line-height: $widget-tag-cloud-height;
            text-transform: uppercase;

            &:after {
                position: absolute;
                content: '';
                left: 0;
                bottom: 0;
                width: 100%;
                height: 1px;
                background-color: $default-heading-color;
            }

            &:hover {
                color: $widget-link-hover-color;
            }
        }
    }

    &.edge-recent-post-widget {
        .edge-recent-posts {
            .edge-rp-item {
                margin: 0 0 13px;
                padding: 0;

                a {
                    display: table;
                    width: 100%;

                    .edge-rp-image,
                    .edge-rp-content {
                        display: table-cell;
                        vertical-align: middle;
                    }

                    .edge-rp-title {
                        @include edgeTransition(color .2s ease-in-out);
                        margin: 0 0 4px;
                    }

                    .edge-rp-image {
                        padding-right: 20px;
                        width: 90px;
                        height: 90px;
                        img {
                            display: block;
                        }
                    }

                    .edge-rp-date {
                        font-size: 13px;
                        font-weight: 400;
                    }

                    &:hover {
                        .edge-rp-title {
                            color: #130a02;
                        }
                    }
                }
            }
        }
    }
}