/* ==========================================================================
   Icon widget style - begin
   ========================================================================== */

.edge-icon-widget-holder {
    position: relative;
    display: inline-block;
    vertical-align: middle;
    margin: 0;

    footer & {
        color: #fff;
    }

    > * {
        position: relative;
        display: inline-block;
        vertical-align: middle;
        color: inherit;
        font-size: inherit;
        line-height: inherit;
        font-style: inherit;
        font-weight: inherit;
        letter-spacing: inherit;
        text-transform: inherit;
    }

    .edge-icon-text {
        padding: 0 0 0 10px;

        &.edge-no-icon {
            padding: 0;
        }
    }
}

.edge-dark-header .edge-page-header > div:not(.edge-sticky-header):not(.fixed) .edge-icon-widget-holder {
    color: $header-dark-color !important;

    &:hover {
        color: $header-dark-hover-color !important;
    }
}

.edge-light-header .edge-page-header > div:not(.edge-sticky-header):not(.fixed) .edge-icon-widget-holder {
    color: $header-light-color !important;

    &:hover {
        color: $header-light-hover-color !important;
    }
}

/* ==========================================================================
   Icon widget style - end
   ========================================================================== */