/* ==========================================================================
   Song widget style - begin
   ========================================================================== */

.edge-song-widget-holder {
    position: relative;
    display: inline-block;
    vertical-align: middle;
    margin: 0;
    color: #fff;

	&.edge-track-playing,
	&:hover {
		color: $first-main-color;
	}

    .edge-song-widget-text-holder,
    .edge-song-widget-icon-holder {
        display: inline-block;
        vertical-align: middle;
        position: relative;
    }

    .edge-song-widget-text-holder {
        margin-right: 21px;
    }

    .edge-song-widget-text {
        color: inherit;
        line-height: inherit;
        font-size: 14px;
        line-height: 28px;
        font-style: inherit;
        font-weight: inherit;
        letter-spacing: 0.1em;
        text-transform: uppercase;
        opacity: 0;
        @include edgeTransition(opacity 0.3s ease);
    }

    .edge-song-widget-icon {
        cursor: pointer;

        > * {
            fill: currentColor;
        }
    }

    .edge-song-widget-icon-play {
        opacity: 1;
        @include edgeTransition(opacity 0.3s ease);
    }

    .edge-song-widget-icon-pause {
        position: absolute;
        top: 50%;
        left: 0;
        @include edgeTransform(translateY(-50%));
        opacity: 0;
        @include edgeTransition(opacity 0.3s ease);
    }

    &.edge-track-in-progress {
        .edge-song-widget-icon-play {
            opacity: 0;
        }
        .edge-song-widget-icon-pause,
        .edge-song-widget-text {
            opacity: 1;
        }
    }

    &.edge-track-paused {
        .edge-song-widget-icon-pause {
            opacity: 0;
        }
        .edge-song-widget-icon-play,
        .edge-song-widget-text {
            opacity: 1;
        }
    }
}

.edge-dark-header .edge-page-header > div:not(.edge-sticky-header):not(.fixed) .edge-song-widget-holder,
.edge-dark-sticky-header .edge-page-header div.edge-sticky-header .edge-song-widget-holder {
    color: $header-dark-color !important;

    &:hover {
        color: $header-dark-hover-color !important;
    }
}

.edge-light-header .edge-page-header > div:not(.edge-sticky-header):not(.fixed) .edge-song-widget-holder,
.edge-light-sticky-header .edge-page-header div.edge-sticky-header .edge-song-widget-holder {
    color: $header-light-color !important;

    &:hover {
        color: $first-main-color !important;
    }
}
/* ==========================================================================
   Song widget style - end
   ========================================================================== */