// widgets spacing
$widget-bottom-margin: 50px;

// list items dimensions
$widget-list-item-height: 20px;
$widget-list-item-padding: 8px;
$widget-title-margin-bottom: 15px;
$widget-specific-title-margin-bottom: 15px;

// list items color
$widget-list-item-background-color: $default-dark-color;

// list items border
$widget-list-item-border-color: #cccccc;
$widget-list-item-border-width: 1px;
$widget-list-item-border-style: solid;

// text
$widget-text-color: #ffffff;
$widget-heading-color: #fff;

// links
$widget-link-color: #fff;
$widget-link-hover-color: rgba(#fff,.7);

// specific widgets
$widget-search-form-height: 52px;
$widget-tag-cloud-height: 20px;
$widget-search-border-color: #fff;

footer {
	@import 'widgets/default/default-wordpress-widgets-flat';
	.edge-footer-top-alignment-center {
		.edge-grid-col-12 {
			.widget.widget_nav_menu ul li {
				display: inline-block;
				margin: 0 20px;
				letter-spacing: 0.1em;

				&:last-child {
					padding: 8px 0;
				}

				a {
					&:after {
						content: '';
						position: absolute;
						bottom: -7px;
						left: 0;
						width: 0;
						height: 1px;
						background-color: $first-main-color;
						@include edgeTransition(width .3s ease);
					}

					&:hover {
						&:after {
							width: 100%;
						}
					}
				}
			}
		}
	}
	.edge-footer-top-holder {

		.widget {
			margin: 0 0 16px;

			p {
				font-size: 12px;
			}

			a {
				color: #fff;

				&:hover {
					color: $widget-link-hover-color;
				}
			}

			ul li {

				&:last-child {
					border: 0;
					padding: 8px 0;
				}
			}

            &.widget_recent_comments,
            &.widget_rss {

				.edge-widget-title-holder {
					margin-bottom: 8px;
				}
            }
		}

		.edge-social-icons-group-widget {

			.edge-social-icon-widget-holder {
				margin: 0 28px;
			}
		}
	}
	.edge-footer-bottom-holder {
		.widget ul .sub-menu {
			padding-left: 0;
		}
	}

	.widget {
		// combobox
		&.widget_archive,
		&.widget_categories,
		&.widget_text {

			select {

				option {
					background: $default-dark-color;
				}
			}
		}
	}

    @include ipad-portrait {
	    .edge-footer-top-alignment-center {
		    .edge-grid-col-12 {
			    .widget.widget_nav_menu ul li {
				    margin: 0 16px;
			    }
		    }
	    }
    }

    @include phone-portrait {
	    .edge-footer-top-alignment-center {
		    .edge-grid-col-12 {
			    .widget.widget_nav_menu ul li {
				    margin: 0;
				    width: 100%;
			    }
		    }
	    }
    }
}