// widgets spacing
$widget-bottom-margin: 45px;

// list items dimensions
$widget-list-item-height: 20px;
$widget-list-item-padding: 15px;
$widget-title-margin-bottom: 25px;
$widget-specific-title-margin-bottom: 25px;

// list items color
$widget-list-item-background-color: $default-dark-color;

// list items border
$widget-list-item-border-color: #cccccc;
$widget-list-item-border-width: 1px;
$widget-list-item-border-style: solid;

// text
$widget-text-color: #ffffff;
$widget-heading-color: #fff;

// links
$widget-link-color: #eeeeee;
$widget-link-hover-color: $first-main-color;

// specific widgets
$widget-search-form-height: 52px;
$widget-tag-cloud-height: 20px;
$widget-search-border-color: #fff;

.edge-side-menu {
    @import 'widgets/default/default-wordpress-widgets-flat';

	.textwidget {
		color: $widget-text-color;

		h1, h2, h3, h4, h5, h6 {
			color: $widget-heading-color;
			margin: 20px 0 8px;
		}

		a {
			color: #fff;
			position: relative;
			letter-spacing: 0.1em;

			&:after {
				content: '';
				position: absolute;
				bottom: -6px;
				left: 0;
				width: 0;
				height: 1px;
				background-color: $first-main-color;
				@include edgeTransition( width .3s ease );
			}

			&:hover {
				color: $first-main-color;

				&:after {
					width: 100%;
				}
			}
		}
	}

	.widget {

		&.edge-social-icons-group-widget {

			a {
				margin: 0 14px 0 0;
				color: #fff;
				font-size: 18px;

				&:hover {
					color: $first-main-color;
				}
			}

			.edge-widget-title-holder {
				margin-bottom: 2px;
			}
		}

		&.widget_media_image {
			margin: 0 0 22px;
		}
	}
}