@import 'variables';
@import 'mixins';

/* ==========================================================================
   #Comments styles - start
   ========================================================================== */

:not(.logged-in) {

    .form-submit {
        margin-toP: 0;
    }
}
.edge-comment-holder {
    @include edgeRelativeHolderLayout();
    padding: 0;
    box-sizing: border-box;

    .edge-comment-holder-inner {
        padding: 35px 0 20px;
    }

    .edge-comment {

        &.edge-pingback-comment {

            .edge-comment-text {
                padding: 0;

	            .edge-comment-info {
		            max-width: 90%;
	            }
            }
        }
    }

    .edge-comment-list {
        list-style: none;
        position: relative;
        z-index: 150;
        padding: 0;
        margin: 0;

        li {
            @include edgeRelativeHolderLayout();
            margin: 30px 0 0;
            padding: 10px 0 0;
        }

        > li:first-child {
            margin: 0;
            padding: 0;
            border: 0;
        }

        .children {
            padding: 0 0 0 110px;
        }
    }

    .edge-comments-title {
        margin-bottom: 41px;

        > * {
            margin: 0;
        }
    }

    .edge-comment-image {
        position: relative;
        display: block;
        top: 7px;
        left: 0;
        width: 110px;
        height: 110px;
        float: left;

        img {
            display: block;
        }
    }

    .edge-comment-text {
        padding: 0 0 0 138px;
        min-height: 82px;

        .edge-comment-info {
            position: relative;
            display: inline-block;
            vertical-align: middle;
        }

        .edge-comment-btns {
            margin-top: 10px;
        }

        .replay,
        .comment-reply-link,
        .comment-edit-link {
            position: relative;
            float: left;
            margin-right: 10px;
            font-family: $default-heading-font;
            font-weight: 500;
            color: $default-heading-color;
            text-transform: uppercase;

            &:after {
                position: absolute;
                display: block;
                content: " ";
                bottom: 3px;
                width: 100%;
                border-bottom: 1px solid #000;
            }
        }

        .replay,
        .comment-reply-link {

            span {
                position: relative;
                display: inline-block;
                top: -1px;
                font-size: 15px;
                margin-left: 5px;
            }
        }

        .edge-comment-name {
            float: left;
            margin: 0;
            font-family: $default-heading-font;
            font-weight: 700;
            font-size: 18px;
            line-height: 1.45em;
            color: $default-heading-color;
            text-transform: uppercase;
        }

        .edge-comment-date {
            margin: 5px 0 16px;
            font-size: 13px;
            line-height: 15px;
        }

        .edge-text-holder p {
            margin: 7px 0;
        }

        .comment-respond {
            margin: 50px 0 20px;

            .comment-reply-title {
                margin: 0 0 17px;
                font-size: 14px;
            }

            .logged-in-as {
                margin: 0 0 8px;
            }
        }

        #cancel-comment-reply-link {
            margin-left: 10px;
            font-size: 12px;
            color: $first-main-color;

            &:hover {
                color: rgba(#000, 0.7);
            }
        }

        ul, ol {

            li {
                display: list-item;
            }
        }

        li {
            margin: 0 !important;
            padding: 0 !important;
            border: 0 !important;
        }
    }
}

.edge-comment-form {
    padding: 0;
    clear: both;
    box-sizing: border-box;

    .edge-comment-form-inner {
        padding: 45px 0 0;
    }

    .comment-reply-title {
        margin: 27px 0 42px;
    }

    .logged-in-as {
        margin: 0 0 8px;
    }
}

@include ipad-portrait {

    .edge-comment-holder {

        .edge-comment-list {

            .children {
                padding: 0 0 0 40px;
            }
        }
    }
}

@include phone-landscape {

    .edge-comment-holder {

        .edge-comment-list {

            .children {
                padding: 0;
            }
        }

        .edge-comment-text {

            .edge-comment-date,
            .edge-comment-name {
                float: none;
            }

            .replay,
            .comment-reply-link,
            .comment-edit-link {
                float: none;
                margin: 0 10px 0 0;
            }
        }
    }
}

@include phone-portrait {

    .edge-comment-holder {

        .edge-comment-image {
            float: none;
        }

        .edge-comment-text {
            padding: 20px 0 0;
            min-height: inherit;
        }
    }
}

/* ==========================================================================
   #Comments styles - start
   ========================================================================== */
input::-webkit-contacts-auto-fill-button {
    visibility: hidden;
    display: none !important;
    pointer-events: none;
    position: absolute;
    right: 0;
}

#respond textarea,
.edge-style-form textarea,
#respond input[type='text'],
input[type='text'],
input[type='email'],
input[type='password'],
.post-password-form input[type='password'] {
    @extend %input-style;
}

.post-password-form input[type='password'] {
    margin-top: 10px;
}

.wpcf7-form-control {
    &.wpcf7-text,
    &.wpcf7-number,
    &.wpcf7-date,
    &.wpcf7-textarea,
    &.wpcf7-select,
    &.wpcf7-quiz {
        @extend %input-style;

        footer & {
            margin: 0;
        }
    }
}

#submit_comment,
.post-password-form input[type='submit'],
input.wpcf7-form-control.wpcf7-submit {
    @include edgeButtonDefaultStyle();
    @include edgeButtonSmallParams();
    @include edgeButtonSolidColor();
    cursor: pointer;

    &:hover {
        @include edgeButtonSolidHoverColor();
    }
}

#submit_comment {
    > i {
        margin: 0 0 0 7px
    }
}

.wpcf7-form p {
    margin: 0;
}

p.form-submit {
    margin: 32px 0 0;
}

#respond textarea,
.edge-style-form textarea,
.wpcf7-form textarea {
    margin-top: 20px;
    display: block;
    padding: 5px;
    resize: none;
    border-bottom: 1px solid $default-dark-color;
}

.comment-form-cookies-consent {
    input {
        margin: 3px 3px 3px 0;
    }

    label {
        position: relative;
        bottom: 1px;
    }
}

.edge-two-columns-form-without-space {

    .wpcf7-form .ajax-loader {
        display: none;
    }
}

/*custom CF7 submit button with icon*/
.edge-cf7-submit-with-icon {
    position: relative;
    display: inline-block;

    input.wpcf7-form-control.wpcf7-submit {
        padding: 10px 37px 10px 21px;
    }

    span.edge-icon {
        font-family: 'FontAwesome';
        font-size: 18px;
        position: absolute;
        right: 43px;
        top: 50%;
        transform: translateY(-50%);
        color: #fff;
    }
}

/* ==========================================================================
   #Comments styles - end
   ========================================================================== */

/* ==========================================================================
   #Global Owl Slider - begin
   ========================================================================== */
.edge-owl-slider {
    visibility: hidden;

    .owl-stage-outer {
        z-index: 1;
    }

    &.edge-slider-is-moving {

        .owl-item {

            .edge-block-drag-link {
                z-index: -1 !important;
            }
        }
    }

    &.edge-slider-has-both-nav {

        .owl-nav {

            .owl-prev,
            .owl-next {
                $pos: calc(-50% - 21px); // 21 is is half height of pagination control + top margin
                @include edgeTransform(translate3d(0,$pos,0));
            }

            .owl-prev {
                &:hover {
                    @include edgeTransform(translate(-5px, calc(-50% - 21px)));
                }
            }

            .owl-next {
                &:hover {
                    @include edgeTransform(translate(5px, calc(-50% - 21px)));
                }
            }
        }
    }

    &.edge-slider-hide-pagination .owl-dots {
        display: none;
    }

    .owl-nav {

        .owl-prev,
        .owl-next {
            position: absolute;
            top: 50%;
            z-index: 3;
            color: $default-heading-color;
            @include edgeTransform(translate3d(0,-50%,0));
            @include edgeTransitionTransform(.2s ease-out);

            > span {
                position: relative;
                display: inline-block;
                vertical-align: middle;
                font-size: 46px;
                width: 1em;
                text-align: center;
                line-height: 1;

                &:before {
                    display: block;
                    line-height: inherit;
                }
            }
        }

        .owl-prev {
            left: 20px;

            &:hover {
                @include edgeTransform(translate3d(-5px,-50%,0));
            }
        }

        .owl-next {
            right: 20px;

            &:hover {
                @include edgeTransform(translate3d(5px,-50%,0));
            }
        }
    }

    .owl-dots {
        text-align: center;
        margin: 50px 0 0;

        .owl-dot {
            display: inline-block;
            vertical-align: middle;

            span {
                display: inline-block;
                vertical-align: middle;
                width: 13px;
                height: 13px;
                border-radius: 50%;
                margin: 0 6px;
                background-color: $first-main-color;
                border: 0;
                box-sizing: border-box;
                @include edgeTransform(scale(0.7));
                @include edgeTransition(background-color .2s ease-out, border .2s ease-out);
            }

            &:hover,
            &.active {

                span {
                    @include edgeTransform(scale(1));
                    background-color: $first-main-color;
                }
            }
        }
    }

    .owl-carousel .owl-item img {
        -webkit-transform: translateZ(0); //chrome text cut off fix
    }

    + .edge-slider-thumbnail {
        display: inline-block;
        vertical-align: middle;
        list-style: none;
        margin: 20px -10px 0;

        > .edge-slider-thumbnail-item {
            float: left;
            padding: 0 10px;
            margin-bottom: 20px;
            box-sizing: border-box;
            line-height: 0;
            cursor: pointer;

            img {
                border: 2px solid transparent;
                @include edgeTransition(border-color 0.2s ease-out);
            }

            &.active {
                img {
                    border: 2px solid $first-main-color;
                }
            }
        }

        $thumbnail_columns_number: ('two', 'three', 'four', 'five', 'six');

        @for $i from 0 to length($thumbnail_columns_number) {
            &.edge-slider-columns-#{nth($thumbnail_columns_number,$i+1)} {

                .edge-slider-thumbnail-item {
                    width: 100% / ($i+2);

                    @include ipad-landscape {
                        width: 33.3333%;
                    }
                }
            }
        }

    }

}

/* ==========================================================================
   #Global Owl Slider - end
   ========================================================================== */

/* ==========================================================================
   #404 styles - start
   ========================================================================== */

.error404 {

    .edge-wrapper {
        padding: 0 !important;
    }

    .edge-content {
        height: 100vh;
    }

    &.admin-bar {

        .edge-content {
            height: calc(100vh - 32px);
        }
    }

    .edge-content-inner {
        @include edgeTableLayout();
        min-height: 100vh;
    }

    .edge-page-not-found {
        @include edgeTableCellLayout();
        padding: 0 32%;
        text-align: center;

        .edge-404-title {
            margin: 0;
        }

        .edge-404-subtitle {
            margin: 35px 0 0;
        }

        .edge-404-text {
            margin: 20px 0;
        }

        .edge-btn {
            margin: 30px 0 0;

            &.edge-btn-light-style {
                color: #585858;
                background-color: #fff;
                border-color: #fff;

                &:hover {
                    color: #fff !important;
                    background-color: transparent !important;
                    border-color: #fff !important;
                }
            }
        }
    }

    @include ipad-landscape {

        .edge-content {
            height: 100%;
            min-height: 100vh;
        }

        &.admin-bar {

            .edge-content {
                height: 100%;
                min-height: calc(100vh - 32px);
            }
        }
    }

    @include ipad-portrait {

        .edge-page-not-found {
            padding: 70px 30px 20px;
        }
    }
}

/* ==========================================================================
   #404 styles - end
   ========================================================================== */

/* ==========================================================================
   #Back to Top styles - start
   ========================================================================== */
#edge-back-to-top {
    position: fixed;
    width: 41px;
    right: 25px;
    bottom: 25px;
    margin: 0;
    z-index: 10000;
    opacity: 0;
    visibility: hidden;

    &.off {
        opacity: 0;
        visibility: hidden;
        @include edgeTransition(opacity 0.15s ease, visibility .0s .15s);
    }

    &.on {
        opacity: 1;
        visibility: visible;
        @include edgeTransition(opacity 0.3s ease);
    }

    &.edge-light {
        > span {
            color: #000;
            background-color: #fff;
            border-color: #fff;

            &:before,
            &:after {
                background-color:#000;
            }
        }
    }

    > span {
        position: relative;
        display: block;
        width: 39px;
        height: 39px;
        line-height: 40px;
        font-size: 15px;
        text-align: center;
        color: #fff;
        background-color: #000;
        border: 1px solid #000;
        overflow: hidden;
        @include edgeTransition(color 0.3s ease-in-out, background-color 0.3s ease-in-out, border-color 0.3s ease-in-out);

        &:before,
        &:after {
            content:"";
            width:12px;
            height:1px;
            background-color:#fff;
            position:absolute;
            top:50%;
            @include edgeTransitionTransform(.2s ease);
        }

        &:before {
            left:10px;
            @include edgeTransform(rotate(-45deg));
        }

        &:after {
            left:18px;
            @include edgeTransform(rotate(45deg));
        }

        &:hover {
            &:before { 
                @include edgeTransform(translate3d(0,-3px,0) rotate(-45deg));
            }

            &:after { 
                @include edgeTransform(translate3d(0,-3px,0) rotate(45deg));
            }
        }
    }

    span span {
        display: block;
        line-height: inherit;
    }
}

/* ==========================================================================
   #Back to Top styles - end
   ========================================================================== */

/* ==========================================================================
   #Self Hosted Video styles - begin
   ========================================================================== */
.edge-self-hosted-video-holder {

    .edge-video-wrap {

        .mejs-poster,
        .mejs-container,
        .mejs-overlay-play {
            height: 100% !important; /*!important because of the inline style*/
            width: 100% !important; /*!important because of the inline style*/
        }

        .mejs-overlay-button {
            font-family: "FontAwesome";
            background: none;
            width: 48px;
            height: 48px;
            text-align: center;
            line-height: 48px;

            &:after {
                content: "\f04b";
                color: #fff;
                font-size: 48px;
            }
        }
    }
}

/* ==========================================================================
   #Self Hosted Video styles - end
   ========================================================================== */

.edge-holder-vertical-pos {

    .edge-elements-holder,
    .wpb_wrapper {
        height: 100%;
    }
}

/* ==========================================================================
   #Other
   ========================================================================== */
.edge-row-marquee {
    margin-bottom: -48px;

    .edge-text-marquee {
        line-height: 1;
    }
}

.edge-frame-shadow {
    iframe {
        box-shadow: 0px 0px 9px 0px rgba(0, 0, 0, 0.17);
    }
}