/* ==========================================================================
   Social Icons Group widget style - begin
   ========================================================================== */

.edge-social-icons-group-widget {

	&.edge-square-icons {

		.edge-social-icon-widget-holder {
			position: relative;
			width: 44px;
			height: 44px;
			margin: 0 7px;
			font-size: 17px;
			color: #fff;
			background-color: $default-heading-color;

			&:hover {
				color: $default-heading-color;
				background-color: $first-main-color;
			}

			.edge-social-icon-widget {
				position: absolute;
				top: 50%;
				left: 50%;
				@include edgeTransform(translateY(-50%) translateX(-50%));
			}
		}

		&.edge-light-skin {

			.edge-social-icon-widget-holder {
				color: $default-heading-color;
				background-color: #fff;

				&:hover {
					color: $default-heading-color;
					background-color: $first-main-color;
				}
			}
		}
	}

	&.edge-has-border {
		position: relative;
		line-height: 1.3;
		padding-bottom: 18px;

		&:after {
			position: absolute;
			display: block;
			bottom: 0;
			left: 50%;
			content: " ";
			width: 100%;
			max-width: 520px;
			border-bottom: 1px solid #363636;
			@include edgeTransform(translateX(-50%));
		}
	}

	.edge-social-icon-widget-holder {
		display: inline-block;
		vertical-align: middle;
		margin: 0 10px 0 0;
		text-align: center;

		&:first-child {
			margin-left: 0;
		}

		&:last-child {
			margin-right: 0;
		}
	}
}
/* ==========================================================================
   Social Icons Group widget style - end
   ========================================================================== */