// widgets spacing
$widget-bottom-margin: 20px;
$widget-bottom-padding: 15px;
$widget-inner-padding: 15px;

// widget color
$widget-background-color: #eeeeee;

// list items dimensions
$widget-list-item-height: 20px;
$widget-list-item-padding: 9px;
$widget-title-margin-bottom: 23px;
$widget-specific-title-margin-bottom: 7px;

// list items color
$widget-list-item-background-color: #ffffff;

// list items border
$widget-list-item-border-color: $default-border-color;
$widget-list-item-border-width: 0;
$widget-list-item-border-style: solid;

// text
$widget-text-color: inherit;
$widget-heading-color: initial;

// links
$widget-link-color: $default-text-color;
$widget-link-hover-color: $default-dark-color;

// specific widgets
$widget-search-form-height: 35px;
$widget-tag-cloud-height: 20px;
$widget-search-border-color: $default-dark-color;

aside.edge-sidebar {
    position: relative;
    z-index: 11;
    margin: 0 0 60px;

    .widget {
        color: $default-text-color;

        &:last-child {
            margin: 0 !important;
        }
    }
}

.wpb_widgetised_column {
    position: relative;
    .widget {

        .edge-sidearea-widget-title {
            margin: 0 0 19px;
        }

        &:last-child {
            margin: 0 !important;
        }
    }
}

aside.edge-sidebar,
.wpb_widgetised_column {
    @import 'widgets/default/default-wordpress-widgets-flat';

    .widget.widget_search .input-holder button:hover,
    .widget.widget_search .input-holder button:hover {
        background-color: transparent;
        border-color: transparent;
    }
}

